.App {
  text-align: center;
}

.black-nav {
  display: flex;
  background: black;
  width: 1920px;
  height: 80px;
  
  color: rgb(245, 245, 235);
  padding-left: 20px;

}

div{
  box-sizing: border-box;
}

.list{
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
}
.dd{
  margin-top: 20px;
  padding: 20px;
  background: rgb(203, 106, 106);
  text-align: left;
}
.modal{
  margin-top: 20px;
  padding: 20px;
  background: rgb(235, 235, 235);
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}